.vcru__imgs {
  display: flex;
  position: relative;

  &::before {
    content: "+101";
    position: absolute;
    right: -12px;
    top: -8px;
    color: #00a267;
  }

  img {
    margin-right: 23px;
  }
}

.vcru {
  display: flex;
  justify-content: center;
  background-color: #FEEBEF;
  padding-top: 60px;
  padding-bottom: 45px;
}

.vcru__link {
  margin-right: 42px;
}

@media (max-width: 1220px) {
  .vcru {
    flex-wrap: wrap;
  }

  .vcru__link {
    padding-bottom: 40px;
  }
}

@media (max-width: $mobile-width) {
  .vcru__imgs {
    img {
      &:first-child {
        display: none;
      }
    }
  }

  .vcru {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
