.header__container {
  display: flex;
  justify-content: space-between;
  padding-bottom: 78px;
  align-items: center;
}

.header__logo-wrapper {
  display: flex;
  align-items: center;
}


.header__logo-wrapper h1 {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 23px;
  /* identical to box height */

  letter-spacing: 0.02em;
  text-transform: uppercase;
}

.header__logo-wrapper p {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  /* identical to box height */
  padding-left: 20px;

  color: rgba(72, 109, 146, 0.6);
}

.header {
  padding-top: 68px;
  padding-right: 78px;
  padding-left: 78px;
  margin-bottom: 17px;
}

.header__nav-block p {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: #000000;

  &:last-child {
    opacity: 0.4;
  }
}

.header__nav-block {
  display: flex;
}

.header__btn {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  margin: 0; /* Убираем отступы */
  background: white; /* Белый цвет фона */
  mix-blend-mode: screen; /* Режим наложения */
}

.header__btn-wrapper {
  position: relative;
  background: linear-gradient(#FC4A4A, #DC26FA);

  &::before {
    content: "";
    position: absolute;
    width: 236px;
    height: 62px;
    left: -39px;
    top: -17px;
    border: 1px solid #EBEDF0;
    border-radius: 50px;
  }
}
