.footer {
  background-color: black;
  padding-bottom: 104px;
  padding-top: 100px;
}

.footer__container {
  padding-right: 170px;
  padding-left: 80px;

  h2 {
    font-style: normal;
    font-weight: normal;
    font-size: 90px;
    line-height: 100px;
    /* identical to box height, or 111% */


    color: rgba(255, 255, 255, 0.4);
  }
}

.footer__link {
  font-style: normal;
  font-weight: 300;
  font-size: 90px;
  line-height: 100px;
  /* identical to box height, or 111% */
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  color: #FFFFFF;
}

.footer__top-block {
  display: flex;
  justify-content: space-between;
}

.footer__link-style {
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 60px;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(to right, red, fuchsia);
}


.footer__link-mob {
  padding-top: 102px;

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    text-transform: lowercase;

    color: rgba(255, 255, 255, 0.4);
  }

   a {
     font-style: normal;
     font-weight: normal;
     font-size: 22px;
     line-height: 34px;
   }
}


.footer__link-addr {
  padding-top: 70px;
  padding-right: 40px;

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    text-transform: lowercase;

    color: rgba(255, 255, 255, 0.4);
  }

  a {
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 34px;
    width: 239px;
  }
}

.footer__contacts-wrapper {
  display: flex;
  justify-content: space-between;
}


.footer__list-option {
  li {
    padding-bottom: 23px;
    padding-right: 12px;
  }
}


.footer__socials {
  padding-top: 228px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 702px;
}


.footer__list-social {
  display: flex;
  flex-direction: column;
  max-height: 200px;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
  max-width: 429px;

  li {
    padding-bottom: 25px;
    padding-right: 12px;
  }
}


.footer__copyright {
  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: rgba(255, 255, 255, 0.4);
    padding-bottom: 20px;
  }

  a {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: rgba(255, 255, 255, 0.4);
  }
}

.footer__brief-link {
  display: flex;
  align-items: flex-end;
}

@media (max-width: 1220px) {
  .footer__container {
    padding-right: 80px;
  }

  .footer__top-write {
    h2 {
      font-size: 70px;
    }

    a {
      font-size: 70px;
    }
  }
}

@media (max-width: $tablet-width) {
  .footer__container {
    padding-left: 20px;
    padding-right: 20px;
  }

  .footer__top-write {
    h2 {
      font-size: 50px;
    }

    a {
      font-size: 50px;
    }
  }
}

@media (max-width: $mobile-width) {
  .footer__top-write {
    display: flex;
    flex-direction: column;
    align-items: center;

    h2 {
      font-style: normal;
      font-weight: normal;
      font-size: 38px;
      line-height: 43px;
      width: 100%;
    }

    a {
      font-style: normal;
      font-weight: 300;
      font-size: 38px;
      line-height: 43px;
      text-align: left;
      width: 100%;
      max-width: 266px;
    }
  }

  .footer__top-block {
    flex-direction: column;
    align-items: center;
  }

  .footer__brief-link {
    padding-top: 50px;
    font-style: normal;
    font-weight: 300;
    font-size: 38px;
    line-height: 43px;
    display: flex;
    justify-content: center;
  }

  .footer__link {
    margin-right: 58px;
  }

  .footer__socials {
    display: none;
  }

  .footer__link-mob {
    padding-top: 60px;
  }

  .footer__link-addr {
    padding-top: 40px;
  }

  .footer__copyright {
    padding-top: 60px;

    p {
      padding-bottom: 30px;
    }
  }
}
