.blog__item-wrapper {
  padding-left: 30px;

  h4 {
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    line-height: 50px;
    padding-bottom: 20px;
  }

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 30px;
  }
}

.blog__logo-item {
  padding-top: 30px;
  padding-bottom: 20px;
  display: flex;
  align-items: center;

  p {
    padding-bottom: 0;
  }
}

.blogpage__item-link {
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(to right, red, fuchsia);
}

//.blogpage__link-wrapper {
//  position: relative;
//  background: linear-gradient(#FC4A4A, #DC26FA);
//}

.blog__item-footer {
  display: flex;
  justify-content: space-between;
  padding-top: 23px;
  padding-bottom: 23px;
  padding-left: 23px;
  padding-right: 23px;
}

.blog__item {
  width: 100%;
  max-width: 714px;
  margin-right: 16px;
  background-color: #ffffff;
  border-radius: 12px;

  &:last-child {
    margin-right: 0;
  }
}

@media (max-width: 1220px) {
  .blog__item-wrapper {
    padding-left: 20px;
    padding-right: 20px;

    h4 {
      font-size: 32px;
    }
  }
}

@media (max-width: $mobile-width) {
  .blog__item {
    margin-bottom: 20px;

    img {
      height: 164px;
    }
  }

  .blog__item-wrapper {
    h4 {
      font-style: normal;
      font-weight: 500;
      font-size: 28px;
      line-height: 36px;
    }
  }

  .blog__logo-item {
    img {
      width: 30px;
      height: 30px;
    }

    h3 {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;

      &::before {
        top: 7px;
        left: 123px;
      }
    }

    p {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 18px;
    }
  }
}
